* {
  margin: 0;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.home {
  display: flex;
  flex-direction: column;
  height: 100vh;
}
.home__header {
  display: flex;
  justify-content: space-between;
  padding: 20px 30px;
  align-items: center;
}
.home__header a {
  margin-right: 20px;
  text-decoration: inherit;
  color: rgba(0, 0, 0, 0.87);
  font-size: 15px;
}

.home__header a:hover {
  text-decoration: underline;
}
.header__right {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 13vw;
}
.header__right > .MuiSvgIcon-root {
  margin-right: 20px;
}
.home__body {
  flex: 1 1;
  display: flex;
  margin-top: 10%;
  flex-direction: column;
}
.home__body > img {
  object-fit: contain;
  height: 100px;
}

.search__input {
  display: flex;
  align-items: center;
  border: 1px solid lightgray;
  border-radius: 999px;
  padding: 10px 20px;
  height: 30px;
  width: 75vw;
  margin: 0 auto;
  margin-top: 40px;
  max-width: 560px;
}
.search__input > input {
  flex: 1 1;
  padding: 10px 20px;
  font-size: medium;
  border: none;
}
.search__input > input:focus {
  outline: 0;
}
.search__inputIcon {
  color: gray;
}
.search__buttons {
  display: flex;
  justify-content: center;
  margin-top: 30px;
}
.search__buttons button {
  margin: 5px;
  padding: 7px 15px;
  background-color: #f8f8f8;
  border: 1px solid white;
  color: #5f6368;
  text-transform: inherit;
}
.search__buttons button:hover {
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.1);
  background-image: -webkit-linear-gradient(top, #f8f8f8, f1f1f1);
  background-color: #f8f8f8;
  border: 1px solid #c6c6c6;
  color: #222;
}
.hidden {
  display: none;
}

.searchPage__header {
  display: flex;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  background-color: white;
  z-index: 100;
  align-items: flex-start;
  padding: 30px;
  border-bottom: 1px solid lightgray;
  padding-bottom: 15px;
}

.searchPage__logo {
  object-fit: contain;
  height: 50px;
  margin-right: 50px;
}

.searchPage__header--body > .search > .search__input {
  margin-top: 0;
  width: unset;
  width: unset;
  margin: unset;
  max-width: unset;
}
.searchPage__options {
  display: flex;
  align-items: center;
  color: grey;
}
.searchPage__options--Left,
.searchPage__options--Right {
  display: flex;
  margin-top: 20px;
}
.searchPage__options a {
  text-decoration: none;
  color: grey;
}
.searchPage__options--Right {
  margin-left: 80px;
}
.searchPage__option {
  display: flex;
  align-items: center;
  margin-right: 20px;
}
.searchPage__option a {
  margin-left: 5px;
}

.searchPage__result {
  margin: 40px 0;
}

.searchPage__resultCount {
  color: #70757a;
  font-size: 14px;
}

.searchPage__results {
  max-width: 650px;
  margin: 20px 0 100px 240px;
}
.searchPage__resultTitle {
  text-decoration: none;
}
.searchPage__resultTitle:hover {
  text-decoration: underline;
}
.searchPage__resultTitle > h2 {
  font-weight: 500;
}
.searchPage__resultSnippet {
  margin-top: 10px;
}

